::after,
::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
  Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  line-height: 1.5;
  font-size: 0.875rem;
  background: linear-gradient(283deg, rgba(167,195,215,1) -40%, rgba(111,77,204,1) 100%);
}

main {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}


ul {
  list-style-type: none;
}
a {
  text-decoration: none;
}

p {
  text-align: center;
}